/* global document, Office */

let _theMailItem = {
  to: "",
  sender: "",
  subject: "",
  date: "",
  cc: "",
  body: "",
  attachments: [],
};

let recipString = "";
let recipientOneEmail = "";
let toArray = [];

Office.onReady((info) => {
  if (info.host === Office.HostType.Outlook) {
    recipString = "";
    document.querySelector("#cflex-init").style.display = "none";

    console.log("Flex365x Outlook Addin: compose");

    document.querySelector("#cflex-app-body").style.display = "flex";
    //updateUI();

    const interval = setInterval(() => {
      const mainBody = document.querySelector("#cflex-app-body");

      if (mainBody.checkVisibility()) {
        // check recips and compare with our saved string
        const item = Office.context.mailbox.item;
        toArray = [];

        let tempRecipString = "";

        item.to.getAsync((a) => {
          for (let i = 0; i < a.value.length; i++) {
            const to = a.value[i];
            if (recipientOneEmail === "") {
              recipientOneEmail = to.emailAddress;
            }

            toArray.push({
              displayName: to.displayName,
              emailAddress: to.emailAddress,
            });
            tempRecipString += to.emailAddress;
          }

          updateComposeUI(tempRecipString != recipString, toArray, recipientOneEmail);
          recipString = tempRecipString;
        });
      } else {
        clearInterval(interval);
      }
    }, 1000);
  }
});

const getEmailString2 = (details) => {
  let retVal = details.displayName;
  if (details.displayName.indexOf("@") === -1) {
    retVal += " (" + details.emailAddress + ")";
  }

  return retVal;

  // return `${details.displayName}${details.displayName.indexOf("@") === -1 ? ` (${details.emailAddress})` : ""}`;
};

const getEMLFile2 = (item) => {
  let emlContents = "";

  const splitLines = (theString, partSize) => {
    let retVal = "";
    const res = [];

    let idx = 0;
    let part = "";
    do {
      part = theString.substr(idx, partSize);
      if (part !== "") {
        res.push(part);
      }
      idx += partSize;
    } while (part !== "");

    if (res.length > -1) {
      retVal = res.join("\r\n");
    }
    return retVal;
  };

  emlContents += `To: ${item.to}\r\nFrom: ${item.sender}\r\nCC: ${item.cc}\r\nSubject: ${item.subject}\r\nDate: ${new Date().toUTCString()}\r\n`;
  emlContents += "Content-Type: multipart/mixed; boundary=--boundary_text_string\r\n";
  emlContents += "\r\n----boundary_text_string\r\nContent-Type: text/html; charset=ISO-8859-1\r\n";
  emlContents += "Content-Transfer-Encoding: quoted-printable\r\n\r\n";

  emlContents += "<html><head></head><body>";

  if (typeof item.body !== "undefined") {
    emlContents += item.body;
  }

  emlContents += "</body></html>";

  for (const attachment of item.attachments) {
    emlContents += "\r\n\r\n----boundary_text_string\r\n";
    emlContents += `Content-Type: ${attachment.contentType}; name="${attachment.name}"\r\n`;
    emlContents += `Content-Disposition: attachment; filename="${attachment.name}"; size=${attachment.size.toString()}\r\n`;
    emlContents += "Content-Transfer-Encoding: base64\r\n\r\n";
    //emlContents += `${attachment.base64Content}`;
    emlContents += `${splitLines(attachment.base64Content, 76)}`;
  }

  emlContents += "\r\n\r\n----boundary_text_string--";

  console.log(emlContents);

  return emlContents;
};

