const updateComposeUI = (refreshPanel, recips, recipientOneEmail) => {
  return new Promise((resolve) => {
    const item = Office.context.mailbox.item;

    const mainBody = document.querySelector("#cflex-app-body");
    const prompt = document.querySelector("#cflex-select-item-prompt");
    const wait = document.querySelector("#cflex-wait");
    const searchLinkToContainer = document.querySelector("#cflex-search-to-container");
    const searchLinkSenderName = document.querySelector("#cflex-search-link-sender-name");
    const searchLinkSenderAddress = document.querySelector("#cflex-search-link-sender-address");

    if (searchLinkToContainer && searchLinkSenderName && searchLinkSenderAddress && mainBody && prompt && wait) {
      if (refreshPanel) {
        mainBody.style.display = "none";
        prompt.style.display = "none";
        wait.style.display = "flex";
        searchLinkToContainer.replaceChildren();
        searchLinkSenderName.replaceChildren();
        searchLinkSenderAddress.replaceChildren();
      }
    } else {
      // can't find our container, we'll bail
      resolve();
    }

    if (!item) {
      wait.style.display = "none";
      prompt.style.display = "flex";
      resolve();
    }

    _theMailItem = {
      to: "",
      sender: "",
      subject: "",
      date: "",
      cc: "",
      body: "",
      attachments: [],
    };

    _theMailItem.sender = `${Office.context.mailbox.userProfile.displayName} (${Office.context.mailbox.userProfile.emailAddress})`;

    const toArray = [];

    recips.forEach((to) => {
      if (recipientOneEmail === "") {
        recipientOneEmail = to.emailAddress;
      }

      if (_theMailItem.to.length > 0) {
        _theMailItem.to += "; ";
      }

      _theMailItem.to += getEmailString2(to);

      //_theMailItem.to += (_theMailItem.to.length > 0 ? "; " : "") + getEmailString2(to);

      toArray.push({
        displayName: to.displayName,
        emailAddress: to.emailAddress,
      });
    });

    item.cc.getAsync((a) => {
      a.value.forEach((cc) => {
        if (_theMailItem.cc.length > 0) {
          _theMailItem.cc += "; ";
        }

        theMailItem.cc += getEmailString2(cc);
      });
      item.body.getAsync(Office.CoercionType.Html, null, (b) => {
        _theMailItem.body = b.value;

        item.subject.getAsync((a) => {
          _theMailItem.subject = a.value;

          const promises = [];

          item.getAttachmentsAsync((atts) => {
            /*
const promises = [];

  list.forEach(x => {
    promises.push(asyncFn(x));
  });

  return Promise.all(promises);

            */

            for (const attachment of atts.value) {
              promises.push(getAttachmentsContent2(item, attachment.id));
            }

            Promise.all(promises).then(() => {
              const dragElement = document.querySelector("#cflex-drag-envelope");
              if (dragElement) {
                dragElement.addEventListener("dragstart", composeDragger);
              }

              // sender
              const senderNameLink = document.createElement("a");
              senderNameLink.href = "javascript:void(0)";
              senderNameLink.innerText = Office.context.mailbox.userProfile.displayName;
              senderNameLink.style.paddingRight = "20px";
              senderNameLink.addEventListener("click", () =>
                openFlexSite(`${Office.context.mailbox.userProfile.displayName}`)
              );

              searchLinkSenderName.replaceChildren();
              searchLinkSenderName.append(senderNameLink);

              const senderNameAddress = document.createElement("a");
              senderNameAddress.href = "javascript:void(0)";
              senderNameAddress.innerText = Office.context.mailbox.userProfile.emailAddress;
              senderNameAddress.addEventListener("click", () =>
                openFlexSite(`${Office.context.mailbox.userProfile.emailAddress}`)
              );

              searchLinkSenderAddress.replaceChildren();
              searchLinkSenderAddress.append(senderNameAddress);

              //flex-search-to-container

              const searchLinkToContainer = document.querySelector("#cflex-search-to-container");
              if (searchLinkToContainer) {
                searchLinkToContainer.replaceChildren();
                toArray.forEach((to) => {
                  if (to.emailAddress !== Office.context.mailbox.emailAddress) {
                    const div = document.createElement("div");

                    const linkName = document.createElement("a");
                    linkName.href = "javascript:void(0)";
                    linkName.innerHTML = `${to.displayName}`;
                    linkName.addEventListener("click", () => openFlexSite(`${linkName.innerHTML}`));

                    div.appendChild(linkName);
                    if (searchLinkToContainer.children.length > 0) {
                      div.style.paddingTop = "10px";
                    }

                    searchLinkToContainer.appendChild(div);

                    if (to.displayName !== to.emailAddress) {
                      const div2 = document.createElement("div");
                      const linkAddress = document.createElement("a");
                      linkAddress.href = "javascript:void(0)";
                      linkAddress.innerHTML = `${to.emailAddress}`;

                      linkAddress.addEventListener("click", () => openFlexSite(`${linkAddress.innerHTML}`));

                      div2.appendChild(linkAddress);
                      searchLinkToContainer.appendChild(div2);
                    }
                  }
                });
              }
            });

            wait.style.display = "none";
            prompt.style.display = "none";
            mainBody.style.visibility = "hidden";
            mainBody.style.display = "flex";

            // set height
            const divElement = searchLinkToContainer;
            divElement.style.resize = "";
            divElement.style.overflow = "";
            divElement.style.height = "auto";

            if (divElement.getBoundingClientRect().height > 120) {
              divElement.style.resize = "vertical";
              divElement.style.overflow = "auto";
              divElement.style.height = "120px";
            }

            mainBody.style.visibility = "";
          });
        });
      });
    });
  });
};

const getAttachmentsContent2 = (item, attId) => {
  return new Promise((resolve) => {
    item.getAttachmentContentAsync(attId, (c) => {
      _theMailItem.attachments.push({
        id: attId,
        name: c.value.name,
        contentType: c.value.contentType,
        size: c.valuet.size,
        base64Content: c.value.content,
      });
      resolve();
    });
  });
};

const composeDragger = (e) => {
  console.log(
    JSON.stringify({
      to: _theMailItem.to,
      sender: _theMailItem.sender,
      subject: _theMailItem.subject,
      date: new Date().toUTCString(),
      cc: _theMailItem.cc,
      body: _theMailItem.body,
      numAttachments: _theMailItem.attachments.length.toString(),
      filename: `${_theMailItem.subject.replace(/[^a-z0-9+]+/gi, "_")}_${new Date().getMilliseconds().toString()}${Math.floor(Math.random() * 1000)}.eml`,
      eml: getEMLFile2(_theMailItem),
    })
  );
  e.dataTransfer.setData(
    "text",
    JSON.stringify({
      to: _theMailItem.to,
      sender: _theMailItem.sender,
      subject: _theMailItem.subject,
      date: new Date().toUTCString(),
      cc: _theMailItem.cc,
      body: _theMailItem.body,
      numAttachments: _theMailItem.attachments.length.toString(),
      filename: `${_theMailItem.subject.replace(/[^a-z0-9+]+/gi, "_")}_${new Date().getMilliseconds().toString()}${Math.floor(Math.random() * 1000)}.eml`,
      eml: getEMLFile2(_theMailItem),
    })
  );
};

function onMessageComposeHandler(event) {
  console.log("onMessageComposeHandler(): entered!");

  var options = { asyncContext: { callingEvent: event } };
  Office.context.mailbox.item.notificationMessages.addAsync(
    "showInfoBarForSampleInstructions",
    {
      type: Office.MailboxEnums.ItemNotificationMessageType.InsightMessage,
      message: "Open the Task Pane to save to Flex",
      icon: "Icon.16x16",
      actions: [
        {
          actionText: "Show Flex365x Task Pane",
          actionType: Office.MailboxEnums.ActionType.ShowTaskPane,
          commandId: "msgComposeOpenPaneButton",
          contextData: "{''}",
        },
      ],
    },
    options,
    function (result) {
      console.log("onMessageComposeHandler(): Notification message added");
      result.asyncContext.callingEvent.completed();
    }
  );
}

Office.actions.associate("onMessageComposeHandler", onMessageComposeHandler);
